.container {
    display: flex;
    justify-content: center;
}
.card {
    height: 400px;
    width: 80%;
    background-color: grey;
    color: white;
    border-radius: 15px;
}

.card img {
    border-radius: 15px;
    object-fit: cover;
    height: 100%;
    object-position: center;
}